.preloader {
    position: absolute;
    width: 100%;
    height: 100%;

    top: 0;
    left: 0;
    background: $bd-header;
    z-index: 109;

    &__wrapper {
        // height: 100%;

        position: fixed;
        transform: translateX(-50%);
        top: 40vh;
        left: 50%;
        z-index: 110;

    }

    &--hide {
        opacity: 0;
        transition: all $speed $cubic-bezier
    }
}

.lds-ring {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
}

.lds-ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 64px;
    height: 64px;
    margin: 8px;
    border: 8px solid $svg-secondary;
    border-radius: 50%;
    animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: $svg-secondary transparent transparent transparent;
}

.lds-ring div:nth-child(1) {
    animation-delay: -0.45s;
}

.lds-ring div:nth-child(2) {
    animation-delay: -0.3s;
}

.lds-ring div:nth-child(3) {
    animation-delay: -0.15s;
}

@keyframes lds-ring {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}